html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  color: #e2e6ec;
  margin: 0 auto;
}

.wrapper {
  font-family: "Lucida Sans";
  font-size: 20px;
  padding-top: 20px;
  max-width: 1200px;
  margin: 0 auto;
  height: 100vh;
}

.input {
  font-size: 25px;
}
